import React, { Component } from 'react'
import '../src/App.css'
class App extends Component {
  state={
    loading: true,
    nasaData: null,    
  }
  async componentDidMount(){
    const url = 'https://api.nasa.gov/planetary/apod?api_key=s3Ro78SfpZQASJOPCYH3Ggcoby7guKm2mVaqoDgq'
    const response = await fetch(url)
    const data = await response.json()
    this.setState({
      nasaData: data, loading: false
    })
    console.log(data)
  }
  render() {

    return (
      <div className={`App`}>        
        <div>{ this.state.loading || !this.state.nasaData ? (
            <div>
              Loading...
            </div>
          ):(
            <div>
              <div><h1>{this.state.nasaData.title}</h1></div>
              <div><img src={this.state.nasaData.url} alt='' /></div>
              <div><h3>{this.state.nasaData.explanation}</h3></div>
            </div>
        )}</div>
      </div>
    )
  }
}

export default App
